<template>
  <div id="app">
    <header>
      <div class="logo">Monster Hunt</div>
    </header>
    <div class="container">
      <div class="you">
        <h2>YOU</h2>
        <div v-bind:style="{ width: playerHealth + '%' }" class="healthbar">
          <span v-if="playerHealth > 0">{{playerHealth}}</span>
          <span v-else>0</span>
        </div>
      </div>

      <div class="monster">
        <h2>MONSTER</h2>
        <div class="healthbar" v-bind:style="{ width: monsterHealth + '%' }">
          <span v-if="monsterHealth > 0">{{monsterHealth}}</span>
          <span v-else>0</span>
        </div>
      </div>
    </div>

    <div class="buttons">
      <transition-group name="move-fade">
        <button key="one" v-if="!gameStarted" class="btn" @click="startGame">{{buttonmsg}}</button>

        <template v-else>
          <button key="five" class="btn orange" @click="action('attack')">Attack</button>
          <button key="two" class="btn teal" @click="action('special')">Special Attack</button>
          <button key="three" class="btn green" @click="action('heal')">Heal</button>
          <button key="four" class="btn red" @click="giveUp()">Give Up</button>
        </template>
      </transition-group>
    </div>

    <transition-group name="movex-fade" tag="ul" class="messages">
      <li v-for="(msg, i) in msgs" :key="i" :class="msg.class">{{ msg.msg }}</li>
    </transition-group>
  </div>
</template>

<script>
// import { setTimeout } from "timers";

export default {
  name: "app",
  data() {
    return {
      gameStarted: false,
      playerHealth: 100,
      monsterHealth: 100,
      msgs: [],

      buttonmsg: "Start New Game",
      again: false
    };
  },
  computed: {},
  methods: {
    startGame() {
      this.gameStarted = true;
      this.playerHealth = 100;
      this.monsterHealth = 100;
      this.msgs = [];
      this.buttonmsg = "Play Again?";
    },

    reset() {
      this.gameStarted = false;
    },

    checkWin() {
      if (this.monsterHealth <= 0) {
        this.msgs = [{ msg: "You Won!", class: "green" }];
        this.reset();
        return true;
      } else {
        if (this.playerHealth <= 0) {
          this.msgs = [{ msg: "You Lost!", class: "red" }];
          this.reset();
        }
      }
    },

    action(action) {
      if (action === "attack") {
        // attack action
        const min = 10;
        const max = 13;
        const x = Math.floor(Math.random() * (max - min) + min);
        this.monsterHealth -= x;
        this.msgs.push({
          msg: `Player hits Monster for ${x}`,
          class: "green"
        });
      } else if (action === "special") {
        // special attack action
        const min = 14;
        const max = 17;
        const s = Math.floor(Math.random() * (max - min) + min);
        this.monsterHealth -= s;
        this.msgs.push({
          msg: `Player crits Monster for ${s}`,
          class: "green"
        });
      } else if (action === "heal") {
        // heal action
        const min = 16;
        const max = 19;
        const h = Math.floor(Math.random() * (max - min) + min);
        this.playerHealth += h;
        // handle max hp
        if (this.playerHealth > 100) {
          this.playerHealth = 100;
        }
        this.msgs.push({ msg: `Player heals for ${h}`, class: "green" });
      }
      // check if won
      if (this.checkWin()) {
        return;
      }

      // monster attacks
      const min = 12;
      const max = 15;
      const m = Math.floor(Math.random() * (max - min) + min);
      this.playerHealth -= m;
      this.msgs.push({
        msg: `Monster hits Player for ${m}`,
        class: "red"
      });

      // check if lost
      this.checkWin();
    },

    giveUp() {
      this.reset();
      this.msgs = [
        {
          msg: "You Gave Up!",
          class: "red"
        }
      ];
    }
  }
};
</script>

